<template>
	<div class="matchWrap">
		<div v-for='(item,i) in f_matches'>
			<div class="left">
				<span>{{item.name}}</span>
				<span>{{item.id}}</span>
				<time>{{item.time}}截止</time>
				<span>析v</span>
			</div>
			<div class="right">
				<div class="team">{{item.host}}&nbsp;&nbsp;&nbsp;&nbsp;VS&nbsp;&nbsp;&nbsp;&nbsp;{{item.guest}}</div>
				<div class="conts">
					<div class="handicap"><span class="top">{{item.odd1}}</span><span class="bot">{{item.odd2}}</span></div>
					<div class="odds">
						<ul class="wdl">
							<li v-for='(val1,index1) in item.wdl_rates' @click='choose1(i,index1)' :class='{red:val1.bol}'>{{val1.wdl+val1.rate.toFixed(2)}}</li>
						</ul>
						<ul class="gwdl">
							<li v-for='(val2,index2) in item.gwdl_rates' @click='choose2(i,index2)' :class='{red:val2.bol}'>{{val2.gwdl+val2.rate.toFixed(2)}}</li>	
						</ul>
					</div>	
					<div class="expand">展开全部</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import Bus from '../bus.js'
	export default {
		data:function(){
			return{
				selectedNum:0,			//变量，用于存储选定的比赛数量
				option:[],					//空数组，存储临时选定的比赛，包含重复选中的比赛
				selectedAll:[],			//空数组，用于存储选定的所有比赛
				n:0
			}
		},
		props:{
			f_matches:{
				type:Array,
				require:true
			}
		},
		methods:{
			choose1:function(i,index1){ 
				//声明变量nowMatch为当前选中的比赛
				var nowMatch = this.f_matches[i];          
				nowMatch.wdl_rates[index1].bol = !nowMatch.wdl_rates[index1].bol;
				//每次点击遍历所有比赛，遍历每场比赛中胜平负和让球胜平负，是否选中，如果bol为ture，n=1作为标记			
				this.n=0;
				this.option=[];
				for(var k=0;k<3;k++){
					if(nowMatch.wdl_rates[k].bol){
						this.n++;
					}if(nowMatch.gwdl_rates[k].bol){
						this.n++;
					}
				};
				//console.log(this.n)
				if(this.n==1){
					for(var k=0;k<this.selectedAll.length;k++){    //判断是否已经添加了该场比赛
						if(this.selectedAll[k].id==nowMatch.id){   //已经添加了该场比赛，n=1是在删除已选的情况下出现的
							this.selectedAll[k].option=[];
							for(var j=0;j<3;j++){
								if(nowMatch.wdl_rates[j].bol){
									if(j==0){this.selectedAll[k].option.push("胜")}
									if(j==1){this.selectedAll[k].option.push("平")}
									if(j==2){this.selectedAll[k].option.push("负")}
								}if(nowMatch.gwdl_rates[j].bol){
									if(j==0){this.selectedAll[k].option.push("让球胜")}
									if(j==1){this.selectedAll[k].option.push("让球平")}
									if(j==2){this.selectedAll[k].option.push("让球负")}
								}
							}
							//console.log(this.selectedAll)
							this.$emit(' toBuy',this.selectedAll);
							 return;
						}
					} 		//对于第一次点击一场比赛，没有添加比赛，添加比赛进数组, 此处将所有比赛第一次的投注项全部加进数组
					this.selectedAll.push({"id":nowMatch.id,"host":nowMatch.host,"guest":nowMatch.guest,"option":[]})	    				         			
				}
				if(this.n==0){
					//console.log(111)
					for(var k=0;k<this.selectedAll.length;k++){
						if(this.selectedAll[k].id==nowMatch.id){
							this.selectedAll.splice(k,1);
						}
					}
				}	
				//console.log(this.selectedAll)
				//遍历选定的比赛，根据id查找投注项
				for(var k=0;k<this.selectedAll.length;k++){
						if(this.selectedAll[k].id==nowMatch.id){
							this.selectedAll[k].option=[];
							if(nowMatch.wdl_rates[0].bol){
								this.selectedAll[k].option.push("胜");								
							}
							if(nowMatch.wdl_rates[1].bol){
								this.selectedAll[k].option.push("平");								
							}
							if(nowMatch.wdl_rates[2].bol){
								this.selectedAll[k].option.push("负");								
							}
							if(nowMatch.gwdl_rates[0].bol){
								this.selectedAll[k].option.push("让球胜");								
							}
							if(nowMatch.gwdl_rates[1].bol){
								this.selectedAll[k].option.push("让球平");								
							}
							if(nowMatch.gwdl_rates[2].bol){
								this.selectedAll[k].option.push("让球负");								
							}
						}
				}
				//console.log(this.selectedAll)
				this.selectedNum=this.selectedAll.length;
				this.$emit('selected',this.selectedNum);
				this.$emit('toBuy',this.selectedAll);
			},
			choose2:function(i,index2){
				var nowMatch = this.f_matches[i]; 
				nowMatch.gwdl_rates[index2].bol = !nowMatch.gwdl_rates[index2].bol;
				//每次点击遍历所有比赛，遍历每场比赛中胜平负和让球胜平负，是否选中，如果bol为ture，n=1作为标记			
				this.n=0;
				this.option=[];
				for(var k=0;k<3;k++){
					if(nowMatch.wdl_rates[k].bol){
						this.n++;
					}if(nowMatch.gwdl_rates[k].bol){
						this.n++;
					}
				};
				//console.log(this.n)
				if(this.n==1){
					for(var k=0;k<this.selectedAll.length;k++){    //判断是否已经添加了该场比赛
						if(this.selectedAll[k].id==nowMatch.id){
							this.selectedAll[k].option=[];
							for(var j=0;j<3;j++){
								if(nowMatch.wdl_rates[j].bol){
									if(j==0){this.selectedAll[k].option.push("胜")}
									if(j==1){this.selectedAll[k].option.push("平")}
									if(j==2){this.selectedAll[k].option.push("负")}
								}if(nowMatch.gwdl_rates[j].bol){
									if(j==0){this.selectedAll[k].option.push("让球胜")}
									if(j==1){this.selectedAll[k].option.push("让球平")}
									if(j==2){this.selectedAll[k].option.push("让球负")}
								}
							}
							this.$emit(' toBuy',this.selectedAll);
							 return;
						}
					}
					this.selectedAll.push({"id":nowMatch.id,"host":nowMatch.host,"guest":nowMatch.guest,"option":[]})	
				}
				if(this.n==0){
					console.log(111)
					for(var k=0;k<this.selectedAll.length;k++){
						if(this.selectedAll[k].id==nowMatch.id){
							this.selectedAll.splice(k,1);
						}
					}
				}	
				//console.log(this.selectedAll)
				//遍历选定的比赛，根据id查找投注项

				for(var k=0;k<this.selectedAll.length;k++){
						if(this.selectedAll[k].id==nowMatch.id){
							this.selectedAll[k].option=[];
							if(nowMatch.gwdl_rates[0].bol){
								this.selectedAll[k].option.push("让球胜");								
							}
							if(nowMatch.gwdl_rates[1].bol){
								this.selectedAll[k].option.push("让球平");								
							}
							if(nowMatch.gwdl_rates[2].bol){
								this.selectedAll[k].option.push("让球负");								
							}
							if(nowMatch.wdl_rates[0].bol){
								this.selectedAll[k].option.push("胜");								
							}
							if(nowMatch.wdl_rates[1].bol){
								this.selectedAll[k].option.push("平");								
							}
							if(nowMatch.wdl_rates[2].bol){
								this.selectedAll[k].option.push("负");								
							}
						}

				}
				//console.log(this.selectedAll)	
				this.selectedNum=this.selectedAll.length;
				this.$emit('selected',this.selectedNum);
				this.$emit(' toBuy',this.selectedAll);
			}
		},
		created:function(){
			var that = this;
			Bus.$on('reload',function(bol){        //接收清空按钮传递过来的指令，清空投注项
				console.log(bol)
				for(var j=0;j<that.f_matches.length;j++){
					for(var i=0;i<that.selectedAll.length;i++){
						if(that.selectedAll[i].id==that.f_matches[j].id){
							that.f_matches[j].wdl_rates[0].bol=bol;
							that.f_matches[j].wdl_rates[1].bol=bol;
							that.f_matches[j].wdl_rates[2].bol=bol;
							that.f_matches[j].gwdl_rates[0].bol=bol;
							that.f_matches[j].gwdl_rates[1].bol=bol;
							that.f_matches[j].gwdl_rates[2].bol=bol;
						}
					}
				}
				if(!bol){
					that.selectedAll = [];
				}			
			})
		},
		mounted:function(){
			Bus.$emit('betNum',this.f_matches.length)        //传递可投注的比赛数量,事件为betNum,tltalBet组件接收
		}
	}
</script>

<style>
	.matchWrap{font-size: 0.3rem;background: #F4F4F4;padding-bottom: 1rem;}
	.matchWrap .left{width: 20%;float: left;padding-top: 0.6rem;color: #7F7F7F;}
	.matchWrap .left span{display: block;}
	.matchWrap .right .team{padding:0.15rem 0;color: #333;}
	.matchWrap .right .conts{display: flex;flex-wrap: wrap;color: #333;}
	.matchWrap .handicap{width: 5%;}
	.matchWrap .handicap span{display: block;height: 0.80rem;line-height: 0.80rem;}
	.matchWrap .handicap .top{background: #C2E5FF}
	.matchWrap .handicap .bot{background: #DDF4A2}
	.matchWrap .odds{width: 82%;}
	.matchWrap .odds ul{display: flex;flex-wrap: wrap;background: #fff;}
	.matchWrap .odds li{width: 33%;height:0.8rem;line-height: 0.8rem;border-right: 1px solid #F4F4F4;}
	.matchWrap .odds li:nth-child(1){border-bottom: 1px solid #F4F4F4;}
	.matchWrap .odds li:nth-child(2){border-bottom: 1px solid #F4F4F4;}
	.matchWrap .odds li:nth-child(3){border-bottom: 1px solid #F4F4F4;}
	.matchWrap .expand{width: 13%;background: #fff;padding-top: 0.4rem;}
	.red{background: #E04241;color: #fff;}
</style>



