import Vue from 'vue'
import Router from 'vue-router'
import Home from '../pages/home/home'
import Togather from '../pages/togather/togather'
import Redlist from '../pages/redlist/redlist'
import Floow from '../pages/floow/floow'
import Mine from '../pages/mine/mine'

import FootLottery from '../pages/home/footLottery.vue'
import BuyList from '../pages/home/buyList.vue'
import Orderform from '../components/orderform.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
     {
      path: '/home',
      name: 'home',
      component: Home
    },
    {
      path: '/footLottery',
      name: 'footLottery',
      component: FootLottery
    },
    {
      path: '/buyList',
      name: 'buyList',
      component: BuyList
    },
    {
      path: '/orderform',
      name: 'orderform',
      component: Orderform
    },
    {
      path: '/redlist',
      name: 'redlist',
      component: Redlist
    },
      {
      path: '/floow',
      name: 'floow',
      component: Floow
    },
    {
      path: '/togather',
      name: 'togather',
      component: Togather
    },
    {
      path: '/mine',
      name: 'mine',
      component: Mine
    }
  ]
})



