<template>
	<div class="footWrap">
		<Matchheader></Matchheader>
		<Totalbet></Totalbet>
		<Matches :f_matches='f_matches'  @selected='getval' @toBuy='getbet'></Matches>
		
		<div class="selcetWrap">
			<button type="button" class="empty" @click="empty()">清空</button>
			<div>
				<p class="selectNum">已选择{{selectedNum}}场比赛</p>
				<p class="note">赔率仅供参考，以出票为准</p>
			</div>
			<button type="button" class="sure" @click="buy()">确定</button>
		</div>

		<span class="remote" v-if="!modalShow">非单关玩法至少选择2场</span>
	</div>
</template>

<script>
	import Matchheader from '../../components/Matchheader.vue'
	import Totalbet from '../../components/totalBet.vue'
	import Matches from '../../components/matches.vue'
	import Bus from '../../bus.js'
	
	export default {
		components:{
			Matchheader,
			Totalbet,
			Matches
		},
		data:function(){ 
			return{
				f_matches:[],
				selectedNum:0,
				betList:[],
				modalShow:true,
				bol:false
			}
		},
		mounted:function(){
			 var that=this
			 this.axios.get('static/data/lotteryData.json').then(function(res){
			 	 //console.log(res)
			 	 that.f_matches = res.data.matches
			 	 //console.log(that.f_matches)
			 })
		},
		methods:{
			getval:function(val){        //接收投注数量，selected事件触发
				this.selectedNum = val;		
			},
			getbet:function(buyList){    //接收投注项，tobuy事件触发
				this.betList = buyList;			
			},
			buy:function(){         //下注购买
				//console.log(this.betList)
				if(this.betList.length<=1){      //非单关玩法时，最少选择2场投注
						this.modalShow = !this.modalShow;
						var that = this;
					setTimeout(function(){
						that.modalShow = !this.modalShow;
					},1000)
					return;
				}
				this.$router.push('/buyList')    //点击购买，跳转至投注页面 
			},
			empty:function(){
				//console.log(999);
				Bus.$emit('reload',this.bol)    //点击清空按钮时，发送清空指令,matches组件接收   
				this.selectedNum = 0;
				this.betList = [];
			}
		},
		destroyed:function(){
			Bus.$emit('getdata',this.betList)      //将投注项传递给投注页面buyList
		}
	}

</script>

<style>
	.selcetWrap{width: 100%;height: 1rem;background: #3D3A41;border-top: 0.01rem solid #ccc;position: fixed;left: 0;bottom: 0;z-index: 3;display: flex;justify-content: space-between;padding: 0.1rem;}
	.selcetWrap>button{padding: 0 0.4rem;border-radius:0.2rem;font-size: 0.30rem;border: none;color: #fff;}
	.selcetWrap .empty{background: #686A69;}
	.selcetWrap .sure{background: #FE4C4C;}
	.selcetWrap .selectNum{text-align: center;line-height: 0.5rem;height: 0.5rem;color: #C6A572;font-size: 0.30rem;}
	.selcetWrap .note{text-align: center;line-height: 0.3rem;height: 0.3rem;color: #fff;font-size: 0.2rem;}
	.footWrap .remote{display: block;padding:0.2rem 0.4rem;background: gray;border-radius:0.2rem;position: absolute;left: 30%;top: 40%;font-size:0.3rem; color: #fff;}
</style>


