<template>
	<div class="listWrap">
		<h3><span><router-link to='footLottery'><img src="../../assets/images/icon_20.png"/></router-link>&nbsp;&nbsp;&nbsp;&nbsp;编辑/重选</span><router-link to=''>合买</router-link></h3>
		<div class="list" v-for='(item,i) in betList'>
			<div class="left"><span>{{item.id}}</span><span class="del" @click='del(i)'>X</span></div>
			<div class="right">
				<h4><span>{{item.host}}</span>VS<span>{{item.guest}}</span></h4>
				<p>{{item.option}}</p>
			</div>
		</div>
		<div class="bet">
			<table>
				<thead>
					<tr>
						<td>投注类型</td>
						<td>倍数</td>
					</tr>
				</thead>
				<tbody>
					<tr>
						<td v-text='txt' @click='betTypes()' class='mtype'></td>
						<td><span class="des" @click='des()'>-</span><input id='ipt' type="text" v-model='times' @keydown='limit()'/><span class="add" @click='add()'>+</span></td>
					</tr>
				</tbody>
			</table>
			<div class="buy">
				<button type="submit" class="optimize">奖金优化</button>
				<div class="bonus"><p>{{num}}注{{times}}倍共{{totalPrice}}元</p><p>预计奖金：{{pBonus}}元</p></div>
				<button type="submit" class="payment" @click="buyNow">购买</button>
			</div>
		</div>

		<div class="betType" v-show='betBol'>
			<dl id='stand'>
				<dt>标准过关</dt>
				<dd v-for='(item,i) in standardType' @click='standard(i)' :class='{red:item.bol}'>{{item.type}}</dd>
			</dl>
			<dl id='combt'>
				<dt>组合过关</dt>
				<dd v-for='(val,index) in combanateType' @click='combanate(index)'>{{val}}</dd>
			</dl>
		</div>          
	</div>
</template>

<script>
	import Bus from '../../bus.js'
	export default {
		data:function(){
			return{
				host:'',
				guest:'',
				betList:[],   //空数组，存储投注内容   
				times:1,      //购买倍数
				txt:'2串1',   //投注方式
				num:1,         //购买注数
				totalPrice:2,  //总价
				pBonus:'',      //预计奖金
				betBol:false,	//投注方式隐藏显示
				standardType:[],     //标准过关
				allCombanateType:['2串1','3串1','3串3','3串4','4串1','4串4','4串5','4串6','4串11','5串1','5串5','5串6','5串10','5串16','5串20','5串26','6串1','6串6','6串7','6串15','6串20','6串22','6串35','6串42','6串50','6串57','7串1','7串7','7串8','7串21','7串35','7串120','8串1','8串8','8串9','8串28','8串56','8串70','8串247'],   //所有组合过关类型
				combanateType:[],      //组合过关  
				betPass:[]       //用于存储选定的过关方式    
			}
		},
		created:function(){
				var that = this
			Bus.$on('getdata',function(data){        //接收投注数据
				//console.log(data)
				that.betList = data;
				that.txt = data.length+'串1';           //默认串关类型
				that.betPass[0] = data.length+'串1';   //默认过关方式
				//console.log(888)
			})
		},
		methods:{
			del:function(i){            //删除比赛
				this.betList.splice(i,1);
				this.txt = this.betList.length+'串1' ;
				if(this.betList.length < 2){
					this.txt = '非单关玩法请至少选择2场';
				}
				//console.log(10)
			},
			des:function(){          //减少倍数
				this.times--;
				if(this.times<=1){
					this.times = 1;
				}
				this.totalPrice = this.num*this.times*2;
			},
			add:function(){          //增加倍数
				this.times++;
				this.totalPrice = this.num*this.times*2;
				if(this.times>=10000){
					this.times = 10000;
				}
			},
			limit:function(){         //限制input中购买倍数
				var ipt = document.getElementById('ipt');
				if(ipt.value>=1000){
					ipt.value = 1000
				}
			},
			betTypes:function(){       //投注类型显示与隐藏
				this.betBol = !this.betBol;
			},
			combanate:function(i){          //组合过关选择
				var combt = document.getElementById('combt');
				var oDd = combt.getElementsByTagName('dd');
				for(var j=0;j<oDd.length;j++){
					oDd[j].className = '';
				}
				oDd[i].className = 'red'; 
				for(var j=0;j<this.standardType.length;j++){     //清空标准过关，标准过关和组合过关只能选一项  
					this.standardType[j].bol = false;
				}
				//选择组合过关时，标准过关选项清零，组合过关一次只能选一个，注数等于选项中第二个数值，比如4串5，注数为5
				this.num = 0;
				this.betPass[0] = oDd[i].innerHTML;         //将选择的过关类型存储到数组betPass
				this.num = oDd[i].innerHTML.slice(2);	 //彩票注数
				this.totalPrice = this.num*this.times*2;
				//console.log(this.betPass)

			},
			standard:function(i){             //标准过关选择    
				this.standardType[i].bol = !this.standardType[i].bol;
				var combt = document.getElementById('combt');
				var oDd = combt.getElementsByTagName('dd');
				for(var j=0;j<oDd.length;j++){        
					oDd[j].className = '';                 //清空组合过关，标准过关和组合过关只能选一项
				}
				var m = this.standardType.length;
				 //根据选择的标准过关类型：2串1、3串1等，利用排列组合函数计算彩票注数，每次点击清零，循环查找bol为真的选项，将注数累加。
				this.num = 0; 
				this.betPass = [];                     
				for(var j=0;j<m;j++){            
					if(this.standardType[j].bol){
						 //m为标准过关选项数组长度，该长度比选择的比赛场次少1，j为循环下标，标准过关选项数组中是从2串1开始的，2串1意思是从所选择的比赛中选2场
						this.num+=this.combination(m+1,j+2); 
						this.betPass.push(this.standardType[j].type);     //将选择的过关类型存储到数组betPass
					}
				}
				this.totalPrice = this.num*this.times*2;
				//console.log(this.betPass)
			},
			factorial:function(m,n){      
				var num = 1;
		        var count = 0;
		        for(var i = m;i > 0;i--){
		            if(count == n){//当循环次数等于指定的相乘个数时，即跳出for循环
		                break;
		            }
		            num = num * i;
		            count++;
		        }
		        return num;
			},
			combination:function(m,n){    //排列组合函数
				return this.factorial(m,n)/this.factorial(n,n);//就是Cmn(上面是n，下面是m)
			},
			buyNow:function(){            //购买
				this.$router.push('/orderform')    //点击购买，跳转至订单详情页
			}
		},
		mounted:function(){                //根据所选投注场次，加载投注类型选项
			var n = this.betList.length;
				for(var i=0;i<n-1;i++){          
					// this.standardType[i] = {"type":2+i,"bol":false};     //标准过关
					 this.standardType.push({"type":2+i+"串1","bol":false});
				}
				switch(n){												//组合过关
					case 2:
					this.combanateType = this.allCombanateType.slice(0,1) 
					break;
					case 3:
					this.combanateType = this.allCombanateType.slice(0,4)
					break;
					case 4:
					this.combanateType = this.allCombanateType.slice(0,9)
					break;
					case 5:
					this.combanateType = this.allCombanateType.slice(0,16) 
					break;
					case 6:
					this.combanateType = this.allCombanateType.slice(0,26)   
					break;
					case 7:
					this.combanateType = this.allCombanateType.slice(0,32) 
					break;
					case 8:
					this.combanateType = this.allCombanateType
					break;
					default:
					this.combanateType = this.allCombanateType
					break;
				}
		},
		destroyed:function(){
			var orderdata = {"matchdata":this.betList,"passdata":this.betPass,"timesdata":this.times,"pricedata":this.totalPrice}
			Bus.$emit('orderdata',orderdata)     //将投注信息传递给订单详情页
		}
	}
</script>

<style>
	.listWrap h3{height: 0.9rem;background: #E04241;font-size: 0.35rem;color: #fff;font-weight: normal;display: flex;justify-content:space-between;align-items: center;padding: 0 0.4rem;}
	.listWrap h3 a{color: #fff;}
	.listWrap h3 img{width: 0.3rem;height: 0.5rem;}
	.listWrap h3 span{display: flex;align-items: center;}
	.listWrap .list{display: flex;font-size: 0.3rem;padding: 0.2rem;border-top:1px dashed #ccc;border-bottom:1px dashed #ccc; margin: 0.5rem;}
	.listWrap .left{width: 20%;display: flex;flex-direction: column;align-items: center;}
	.listWrap .left span{margin: 0.1rem;}
	.listWrap .del{width: 0.4rem;height: 0.4rem;border-radius: 50%;background: #ccc;color: #fff;line-height: 0.4rem;}
	.listWrap .right{width: 70%;}
	.listWrap .right h4{display: flex;justify-content: space-between;padding: 0.1rem;}
	.listWrap .right p{text-align: left;}
	.listWrap .bet{height: 2.5rem;font-size: 0.3rem;position: fixed;bottom: 0;background: #fff;z-index: 3;}
	.listWrap .bet,.listWrap table,.listWrap table thead,.listWrap table tbody{width: 100%;}
	.listWrap table{border-collapse: collapse; }
	.listWrap table td,.listWrap table tr{width: 50%;border: 1px solid #ccc;}
	.listWrap table tr{height: 0.8rem; }
	.listWrap input{width: 0.8rem;height: 0.5rem;text-align: center;}
	.listWrap table span{padding: 0.2rem;font-size: 0.5rem;color: #E04241;}
	.listWrap table .mtype{position: relative;}
	.listWrap table .mtype:after{content: '';width: 0;height: 0;border-bottom: 0.3rem solid #ccc;border-left: 0.3rem solid transparent;position: absolute;right: 0;bottom:0;}
	.listWrap button{padding: 0.15rem 0.4rem;border-radius:0.2rem;font-size: 0.30rem;border: none;color: #333;font-weight: bold;}
	.listWrap .bet .buy{display: flex;justify-content: space-between;padding:0.1rem;}
	.listWrap .bet .buy{background: #444;}
	.listWrap .bet .optimize{background: #DCD8D5;} 
	.listWrap .bet .payment{background: #F9C31B;}
	.listWrap .bet .bonus{color: #fff;font-size: 0.25rem;}
	.listWrap .betType{position: absolute;top: 29%;font-size: 0.30rem;background: #f7f7f7;width:100%;padding-bottom:2.5rem;}
	.listWrap .betType dl{border: 1px solid #ccc;position: relative;width: 100%;display: flex;flex-wrap:wrap;padding: 0.4rem 0.2rem;}
	.listWrap .betType dt{position: absolute;left: 0.1rem;top:-0.2rem;background: #fff;padding: 0 0.1rem;border: 1px solid #999;}
	.listWrap .betType dd{width: 1.3rem;height: 0.8rem;line-height: 0.8rem;border: 1px solid #ccc;margin: 0.05rem;border-radius:0.1rem; }
</style>


