<template>
	<div class="totalWrap">
		<span>{{year}}年{{month}}月{{day}}日</span>
		<span>星期{{week}}</span>
		<span>{{num}}场可投注</span>
	</div>
</template>

<script>
	import Bus from '../bus.js'
	export default {
		data:function(){
			return {
				year:'',
				month:'',
				day:'',
				week:'',
				num:0
			}
		},
		mounted:function(){
			var date = new Date();
			this.year = date.getFullYear();
			this.month = date.getMonth()+1;
			this.day = date.getDate();
			if(date.getDay()==0)this.week="日";
			if(date.getDay()==1)this.week="一";
			if(date.getDay()==2)this.week="二";
			if(date.getDay()==3)this.week="三";
			if(date.getDay()==4)this.week="四";
			if(date.getDay()==5)this.week="五";
			if(date.getDay()==6)this.week="六";
		},
		created:function(){
			var that = this;
			Bus.$on('betNum',function(num){
				that.num = num;
			})
		}
	}
</script>

<style>
	.totalWrap{padding: 0.1rem 0;font-size: 0.3rem;text-align: left;}
	.totalWrap span{padding: 0 0.1rem;}

</style>


