<template>
	<div class="orderWrap">
		<h3><router-link to='footLottery'><img src="../assets/images/icon_20.png"/></router-link>订单详情<span></span></h3>
		<div class="title">
			<p>订单号：{{orderid}}(<span>待支付</span>)</p>
			<p>投注金额：<span>{{totalPrice}}</span>元</p>
			<p>投注时间：{{date+'&nbsp;&nbsp;'+time}}</p>
		</div>
		<div class="details">
			<div class="tit">
				<h4>选号方案</h4>
				<p>已选场次：<span>{{num}}场</span></p>
				<p>过关方式：<span v-for='item in passdata'>{{item}}&nbsp;&nbsp;</span></p>
				<p>倍数：<span>{{times}}倍</span></p>
			</div>
			<table>
				<thead>
					<tr>
						<th>场次</th>
						<th>主队</th>
						<th>客队</th>
						<th>投注项</th>
						<th>胆码</th>
					</tr>
				</thead>
				<tbody>
					<tr v-for='item in matchdata'>
						<td>{{item.id}}</td>
						<td>{{item.host}}</td>
						<td>{{item.guest}}</td>
						<td><span v-for='val in item.option'>{{val|Comma}}</span></td>
						<td>{{''}}</td>
					</tr>
				</tbody>
			</table>
		</div>
		<div class="explain">
			<p>1、页面中投注奖金仅供参考，实际奖金以店主出票后奖金为准。</p>
			<p>2、赔率/让分及大小分会有变化，以实际出票为准。</p>
			<div>
				<button>取消订单</button>
				<button class="buy">预约取票</button>
			</div>
		</div>
	</div>
</template>

<script >
	import Bus from '../bus.js'
	export default{
		data:function(){
			return{
				matchdata:[],       //比赛场次
				passdata:[],		//过关类型
				num:0,               //已选场次
				times:1,               //倍数
				totalPrice:0,          //购买金额
				date:'', 			//出票日期
				time:''	,			//出票时间
				orderid:''
			}
		},
		created:function(){
			var that = this;
			Bus.$on('orderdata',function(data){          //接收订单信息
				that.matchdata = data.matchdata;
				that.num = data.matchdata.length;
				that.passdata = data.passdata;
				that.times = data.timesdata;
				that.totalPrice = data.pricedata;
				//console.log(data)
			});
		},
		filters:{
			Comma:function(val){
				return val=val+',';
			}
		},
		// methods:{
		// 	padLeftZero:function(str){
		// 		return ('00'+str).substr(str.length);
		// 	},
		// 	formatDate:function(date,fmt){
		// 		if(/(y+)/.test(fmt)){
		// 	        fmt = fmt.replace(RegExp.$1, (date.getFullYear()+'').substr(4-RegExp.$1.length));
		// 	    }
		// 	    let o = {
		// 	        'M+': date.getMonth()+1,
		// 	        'd+': date.getDate(),
		// 	        'h+': date.getHours(),
		// 	        'm+': date.getMinutes(),
		// 	        's+': date.getSeconds()
		// 	    }
		// 	    for(let k in o){    
		// 	        let str = o[k]+'';
		// 	        if(new RegExp('(${k})').test(fmt)){
		// 	            fmt = fmt.replace(RegExp.$1, (RegExp.$1.length===1)?str:this.padLeftZero(str));
		// 	        }
		// 	    }
		// 	    return fmt;
		// 	}
		// },
		mounted:function(){
			this.date = new Date().toLocaleString().substr(0,10);
			this.time = new Date().toTimeString().substr(0,9);
			this.orderid = new Date().getTime();
		}
	}
</script>

<style>
	.orderWrap h3{height: 0.9rem;background: #E04241;font-size: 0.33rem;color: #fff;font-weight: normal;display: flex;justify-content:space-between;align-items: center;padding: 0 0.4rem;}
	.orderWrap h3 a{color: #fff;font-size: 0.5rem;}
	.orderWrap h3 img{width: 0.3rem;height: 0.5rem;}
	.orderWrap .title{font-size: 0.25rem;padding:0.2rem;padding-left: 2rem;text-align: left;border-bottom: 0.01rem solid #ccc;line-height: 0.5rem;}
	.orderWrap .details{font-size: 0.25rem;padding: 0 0.1rem;}
	.orderWrap .details .tit{text-align: left;padding: 0.2rem 0.4rem 0;line-height: 0.5rem;}
	.orderWrap table{width: 100%;border: 0.01rem solid #ccc; border-collapse: collapse;}
	.orderWrap table th,.orderWrap table td{border: 0.01rem solid #ccc;border-collapse: collapse;padding: 0.2rem 0;}
	.orderWrap table th,.orderWrap table td:nth-child(1){width:10% }
	.orderWrap table th,.orderWrap table td:nth-child(2){width:27% }
	.orderWrap table th,.orderWrap table td:nth-child(3){width:27% }
	.orderWrap table th,.orderWrap table td:nth-child(4){width:27% }
	.orderWrap table th,.orderWrap table td:nth-child(5){width:9% }
	.orderWrap .explain{font-size: 0.2rem;text-align: left;padding: 0.2rem 0.1rem;position: fixed;bottom: 0;width: 100%;background: #f7f7f7;}
	.orderWrap .explain>div{display: flex;justify-content: space-between;padding: 0.2rem 0.5rem 0;}
	.orderWrap .explain button{padding: 0.15rem 0.6rem;border: none;color:#fff;background: #ccc;border-radius: 0.1rem;font-size: 0.3rem;}
	.orderWrap .explain .buy{background: #FE4C4C;}
	.orderWrap span{color:#FE4C4C; }
</style>


