<template>
	<div class="m_headerWrap">
		<div class="mheader">		
			<router-link to='home' class='back'><img src="../assets/images/icon_20.png"/></router-link>
			<span class="type" @click="con_bol=!con_bol">{{Type}}&nbsp;</span>
		</div>
		<ul v-show='con_bol'>
			<li v-for='(item,i) in lotType' v-text='item.type' @click='choose(i)' :class='{red:item.bol}'></li>
		</ul>	
	</div>
</template>

<script>
	export default {
		data:function(){
			return {
				lotType:[
					{"type":"胜平负","bol":false},{"type":"让球胜平负","bol":false},{"type":"比分","bol":false},{"type":"总进球","bol":false},
					{"type":"半全场","bol":false},{"type":"2选1","bol":false},{"type":"混合过关","bol":true},{"type":"单关","bol":false},
				],
				Type:'混合过关',
				con_bol:false
			}
		},
		methods:{
			choose:function(i){
				this.Type = this.lotType[i].type;
				this.con_bol = false;
				this.lotType.forEach(function(item){
					item.bol = false;
				})
				this.lotType[i].bol = !this.lotType[i].bol;
			}
		}
	}
</script>

<style>
	.m_headerWrap{height: 1.2rem;background: #E04241;font-size: 0.4rem;padding: 0 0.2rem;color: #fff;}
	.m_headerWrap .mheader{display: flex;justify-content:flex-start;align-items: center;}
	.m_headerWrap .back img{width: 0.3rem;height: 0.6rem;}
	.type{height: 1.2rem;line-height: 1.2rem;padding-left: 0.8rem;}
	.type:after{content: '';width: 0.02rem;height: 0.02rem;border:0.2rem solid transparent;border-top-color:#fff;position: absolute;top:0.5rem;border-radius:0.05rem;}
	.m_headerWrap ul{color: #666;display: flex;flex-wrap: wrap;}
	.m_headerWrap ul li{width: 25%;font-size: 0.32rem;padding:0.2rem 0;border:0.01rem solid #ccc;background: #fff;z-index: 3}
	.m_headerWrap ul li.red{background: #E04241;color: #fff;}

</style>



