<template id="myheader">
	<div id="headerWrap">
		<h3>{{txt}}竞彩实体店</h3>
		<p>{{num}}.87.cn</p>
	</div>
</template>

<script>
	export default {
		data:function(){
			return {
				num:26047,
				txt:"微笑"
			}
		}
	}
		
</script>

<style>
	*{margin: 0;padding: 0;box-sizing:border-box;}
	#headerWrap{width: 100%;height: 1.2rem;background:#E04241;text-align: center;position: relative;color: #fff;font-weight: normal;}
	#headerWrap h3{font-size: 0.40rem;line-height:0.8rem;}
	#headerWrap p{font-size: 0.35rem;line-height:0.3rem;}
</style>


