<template>
	<div class="swiper-container" :class='swipeid'>
		<div class="swiper-wrapper">
			<slot name='swiper-con'></slot>
		</div>
	</div>
</template>

<script>
	import '../assets/libs/swiper/js/swiper.min.js'  

	export default {
		props:{
			swipeid:{
				type:String,
				default:''
			},
			effect:{
				type:String,
				default:'slide'
			},
			loop:{
				type:Boolean,
				default:true
			},
			direction:{
				type:String,
				default:'horizontal'
			},
			autoplay:{
				type:Number,
				default:5000
			},
			slidesPerView:{
				type:[Number,String],
				default:'auto'
			},
			loopedSlides:{
				type:Number,
				default:1
			}
		},
		mounted:function(){
			var that=this;
			new Swiper('.'+that.swipeid,{
				loop:that.loop,
				autoplay:that.autoplay,
				direction:that.direction,
				effect:that.effect,
				slidesPerView:that.slidesPerView,
				loopedSlides:that.loopedSlides,
				autoplayDisableOnInteraction:false
			});
		}
	}
</script>

<style>
	/*引入swiper轮播插件css文件,分号必须写*/
	@import '../assets/libs/swiper/css/swiper.min.css';
	.swiper-container img{width: 100%;}
	.swiper-container .swipe02 .swiper-slide{width:100px;}
	.swiper-container.swipe04 .swiper-slide{width: 300px;margin: 0 20px;}
</style>


