<template>
	<div class="itemWrap" @click='sel()'>
		<span v-show='!bol'><slot name='normalImg'></slot><br/></span>
		<span v-show='bol'><slot name='activeImg'></slot><br/></span>
		<span :class={green:bol}>{{text}}</span>
	</div>
</template>

<script>
	export default {
		props:['text','mark','select'],
		methods:{
			sel:function(){
				this.$emit('change',this.mark)
				this.$router.push('/'+this.mark)
			}
		},
		computed:{
			bol:function(){
				return this.mark==this.select?true:false
			}
		}

	}
</script>

<style type="text/css">
	.itemWrap{width: 20%;float: left;font-size: 0;}
	.itemWrap img{width:0.8rem;}
	.itemWrap span{font-size:0.30rem;color: #999;line-height: 0.26rem;}
	.itemWrap .green{color: #46BD5D}
</style>


